import React, { Component } from 'react'
import dataList from "@/mock/point/"
import LazyImage from "@/components/lazyImage/"
import "./style/statistic.css"
import Bread from "@/components/Bread/"

class Statistic extends Component {
    render() {
        return (
            <div className="statistic-box">
                <Bread history={this.props.history}/>
                <ul>
                    <li>积分值浏览</li>
                    <li>0~1000分</li>
                    <li>1000~2000分</li>
                    <li>2000~3000分</li>
                    <li>3000~5000分</li>
                    <li>5000~10000分</li>
                    <li>10000分以上</li>
                </ul>
                <div className="content">
                    <div className="top">
                        精品推荐
                    </div>
                    <div className="shop-box">
                        {
                            dataList.dataList.map((item, index) => <div key={index} className="shop-item">
                                <div className="img-box">
                                    <LazyImage data_src={item.img} />
                                </div>
                                <div className="con">
                                    <p>{item.title}</p>
                                    <p>积分： <b>{item.num}</b></p>
                                </div>
                            </div>)
                        }

                    </div>
                </div>
            </div>
        )
    }
}


export default Statistic;
